import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {Router} from "@angular/router";
import { FuseConfigService } from '@fuse/services/config.service';
import { fuseAnimations } from '@fuse/animations';
import { AuthenticationService } from '../authentication/authentication.service';
import { ToasterService } from '@fuse/services/toaster-service.service';

@Component({
  selector: 'verify-otp',
  templateUrl: './verify-otp.component.html',
  styleUrls: ['./verify-otp.component.scss'],
  animations : fuseAnimations
})
export class VerifyOtpComponent implements OnInit {
    verifyOtpForm: FormGroup;
    verifyOtpFormErrors: any;

    constructor(
        private fuseConfig: FuseConfigService,
        private formBuilder: FormBuilder,
        private router: Router,
        private authService: AuthenticationService,        
        private apiloader: ApiLoaderService,
        private toast: ToasterService
    )
    {
        this.fuseConfig.setConfig({
            layout: {
                navigation: 'none',
                toolbar   : 'none',
                footer    : 'none'
            }
        });

        this.verifyOtpFormErrors = {
            otp  : {},
            email: {},
            country_code  : {}
        };
    }

  ngOnInit() {
    this.verifyOtpForm = this.formBuilder.group({
        otp          : ['', Validators.required],
        email        : ['', [Validators.required, Validators.email]],
        country_code : ['', Validators.required]
    });

    this.verifyOtpForm.valueChanges.subscribe(() => {
        this.onVerifyFormValuesChanged();
    });
  }

  onVerifyFormValuesChanged()
    {
        for ( const field in this.verifyOtpFormErrors )
        {
            if ( !this.verifyOtpFormErrors.hasOwnProperty(field) )
            {
                continue;
            }

            // Clear previous errors
            this.verifyOtpFormErrors[field] = {};

            // Get the control
            const control = this.verifyOtpForm.get(field);

            if ( control && control.dirty && !control.valid )
            {
                this.verifyOtpFormErrors[field] = control.errors;
            }
        }
    }

    onSubmit(formData){
        this.apiloader.setLoader(true);
        const body = new URLSearchParams();
        body.set('email', formData.email);
        body.set('otp', formData.otp);
        body.set('country_code', formData.country_code);
        body.set('purpose', 'PASSWORD_RESET');

        this.authService.postData('verify', body.toString()).subscribe(response => {
            if (response.message == 'Verified'){
                sessionStorage.setItem('token', response.token); 
                this.apiloader.setLoader(false);
                this.router.navigate(['/pages/auth/reset-password']);
            }
            this.toast.showSuccess(response.message);
        }, error => {
            this.apiloader.setLoader(false);
        });
    }

}
