import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { MatButtonModule, MatCheckboxModule, MatFormFieldModule, MatIconModule, MatInputModule, MatToolbarModule } from '@angular/material';
import { FuseSharedModule } from '@fuse/shared.module';
import { FuseRegisterComponent } from './register.component';
import { FileUploadModule } from 'ng2-file-upload';
import { ImageCropperModule } from 'ngx-image-cropper';
import { ImgCropperComponent } from './img-cropper/img-cropper.component';
import { TermsConditionComponent } from './terms-condition/terms-condition.component';
import { AuthenticationService } from '../authentication.service';

const routes = [
    {
        path     : 'auth/register',
        component: FuseRegisterComponent
    }
];

@NgModule({
    declarations: [
        FuseRegisterComponent,
        ImgCropperComponent,
        TermsConditionComponent
    ],
    imports     : [
        RouterModule.forChild(routes),
        MatButtonModule,
        MatCheckboxModule,
        MatFormFieldModule,
        FileUploadModule,
        MatInputModule,
        MatIconModule,
        MatToolbarModule,
        ImageCropperModule,
        FuseSharedModule
    ],
    entryComponents:[ImgCropperComponent, TermsConditionComponent],
    providers: [ AuthenticationService ]
})
export class RegisterModule
{
}
