import { Component, OnInit, EventEmitter } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { FuseConfigService } from '@fuse/services/config.service';
import { fuseAnimations } from '@fuse/animations';
import { FileUploader, FileItem } from 'ng2-file-upload';
import { MatDialog, MatDialogRef } from '@angular/material';
import { ImgCropperComponent } from './img-cropper/img-cropper.component';
import { TermsConditionComponent } from './terms-condition/terms-condition.component';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ToasterService } from '@fuse/services/toaster-service.service';
import { AuthenticationService } from '../authentication.service';
import { Router } from '@angular/router';

@Component({
    selector   : 'fuse-register',
    templateUrl: './register.component.html',
    styleUrls  : ['./register.component.scss'],
    animations : fuseAnimations
})
export class FuseRegisterComponent implements OnInit
{
    public uploader: FileUploader;
    public filePreviewPath: SafeUrl;
    registerForm: FormGroup;
    registerFormErrors: any;
    imageChangedEvent: any = '';
    croppedImage: any = '';
    showCropper = false;
    dialogRef: any;
    agreeTerms: boolean;

    constructor(
        private fuseConfig: FuseConfigService,
        private formBuilder: FormBuilder,
        public dialog: MatDialog,
        private sanitizer: DomSanitizer,
        private authService: AuthenticationService,
        private apiloader: ApiLoaderService,
        private toast: ToasterService,
        private router: Router
    )
    {
        this.agreeTerms = false;
        this.uploader = new FileUploader({
            url: 'http://localhost/img/upload.php',
            disableMultipart: false,
            autoUpload: false
        });

        this.fuseConfig.setConfig({
            layout: {
                navigation: 'none',
                toolbar   : 'none',
                footer    : 'none'
            }
        });

        this.registerFormErrors = {
            first_name   : {},
            company_name   : {},
            last_name   : {},
            email          : {},
            password       : {},
            passwordConfirm: {}
        };

        this.uploader.onAfterAddingFile = (fileItem) => {
            this.filePreviewPath  = this.sanitizer.bypassSecurityTrustUrl((window.URL.createObjectURL(fileItem._file)));
        }

    }

    ngOnInit()
    {
        this.registerForm = this.formBuilder.group({
            first_name      : ['', Validators.required],
            last_name       : ['', Validators.required],
            company_name    : ['', Validators.required],
            email           : ['', [Validators.required, Validators.email]],
            country_code    : [''],
            contact_number  : [''],
            password        : ['', Validators.required],
            passwordConfirm : ['', [Validators.required, confirmPassword]],
            address         : [''],
            website         : [''],
            latitude         : [''],
            longitude         : [''],
            terms           : ['']
        });

        this.registerForm.valueChanges.subscribe(() => {
            this.onRegisterFormValuesChanged();
        });
    }

    fileChangeEvent(event: any): void {
        this.imageChangedEvent = event;
        this.dialogRef = this.dialog.open(ImgCropperComponent, {
            panelClass: 'contact-form-dialog',
            data      : {
                action: event,
                height: '400px'
            }
        });

        this.dialogRef.afterClosed()
            .subscribe(response => {
                if ( !response )
                {
                    this.filePreviewPath = undefined;
                }else{
                    this.croppedImage = response;
                }
            });
    }
    imageLoaded() {
        this.showCropper = true;
        // show cropper
    }
    cropperReady() {
        // cropper ready
    }
    loadImageFailed() {
        // show message
    }

    public onFileSelected(event: EventEmitter<File[]>) {
        const file: File = event[0];
    }

    onRegisterFormValuesChanged()
    {
        for ( const field in this.registerFormErrors )
        {
            if ( !this.registerFormErrors.hasOwnProperty(field) )
            {
                continue;
            }

            // Clear previous errors
            this.registerFormErrors[field] = {};

            // Get the control
            const control = this.registerForm.get(field);

            if ( control && control.dirty && !control.valid )
            {
                this.registerFormErrors[field] = control.errors;
            }
        }
    }

    checkTerms(event){
        if(event){
            this.dialogRef = this.dialog.open(TermsConditionComponent, {
                panelClass: 'terms-condition-dialog',
                data      : { }
            });
            this.dialogRef.afterClosed()
            .subscribe(response => {
                if(response == true){
                    this.agreeTerms = true;
                }else{
                    this.registerForm.patchValue({'terms': false});
                    this.agreeTerms = false;
                }
            });
        }else{ this.agreeTerms = false; }
    }

    registerClient(formData){
        this.apiloader.setLoader(true);
        delete formData['passwordConfirm'];
        delete formData['terms'];

        const body = new URLSearchParams();
        /* body.set('profile', this.croppedImage); */
        body.set('password', formData.password);
        body.set('email', formData.email);
        body.set('first_name', formData.first_name);
        body.set('last_name', formData.last_name);
        body.set('company_name', formData.company_name);
        body.set('website', formData.website);
        body.set('latitude', formData.latitude);
        body.set('longitude', formData.longitude);

        this.authService.postData('clients/register', body.toString()).subscribe(response => {
            if (response.data){
                this.apiloader.setLoader(false);
                this.toast.showSuccess('Client Registered Successfully!.');
                this.router.navigate(['/pages/auth/login']);
            }
        }, error => {
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }
}

function confirmPassword(control: AbstractControl)
{
    if ( !control.parent || !control )
    {
        return;
    }
    const password = control.parent.get('password');
    const passwordConfirm = control.parent.get('passwordConfirm');

    if ( !password || !passwordConfirm )
    {
        return;
    }

    if ( passwordConfirm.value === '' )
    {
        return;
    }

    if ( password.value !== passwordConfirm.value )
    {
        return {
            passwordsNotMatch: true
        };
    }
}
