import { Router } from '@angular/router';
import { AuthenticationService } from '../authentication.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { FuseConfigService } from '@fuse/services/config.service';
import { fuseAnimations } from '@fuse/animations';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ToasterService } from '@fuse/services/toaster-service.service';

@Component({
    selector   : 'fuse-forgot-password',
    templateUrl: './forgot-password.component.html',
    styleUrls  : ['./forgot-password.component.scss'],
    animations : fuseAnimations
})
export class FuseForgotPasswordComponent implements OnInit
{
    forgotPasswordForm: FormGroup;
    forgotPasswordFormErrors: any;

    constructor(
        private fuseConfig: FuseConfigService,
        private formBuilder: FormBuilder,
        private apiloader: ApiLoaderService,
        private toast: ToasterService,
        private router: Router,
        private authService: AuthenticationService
    )
    {
        this.fuseConfig.setConfig({
            layout: {
                navigation: 'none',
                toolbar   : 'none',
                footer    : 'none'
            }
        });

        this.forgotPasswordFormErrors = {
            email: {}
        };
    }

    ngOnInit()
    {
        this.forgotPasswordForm = this.formBuilder.group({
            email: ['', [Validators.required, Validators.email]]
        });

        this.forgotPasswordForm.valueChanges.subscribe(() => {
            this.onForgotPasswordFormValuesChanged();
        });
    }

    onForgotPasswordFormValuesChanged()
    {
        for ( const field in this.forgotPasswordFormErrors )
        {
            if ( !this.forgotPasswordFormErrors.hasOwnProperty(field) )
            {
                continue;
            }

            // Clear previous errors
            this.forgotPasswordFormErrors[field] = {};

            // Get the control
            const control = this.forgotPasswordForm.get(field);

            if ( control && control.dirty && !control.valid )
            {
                this.forgotPasswordFormErrors[field] = control.errors;
            }
        }
    }

    onSubmit(formData){
        this.apiloader.setLoader(true);
        const body = new URLSearchParams();
        body.set('email', formData.email);

        this.authService.postData('clients/forgot-password', body.toString()).subscribe(response => {
            if (response.data.otp){
                this.apiloader.setLoader(false);
                this.toast.showSuccess('OTP sent to your email address.');
                this.router.navigate(['/pages/verify-otp']);
            }
        }, error =>{
            this.apiloader.setLoader(false);
            this.toast.showError(error);
        });
    }
}
